.dropdown {

  &__label {
    margin-bottom: 5px;
  }

  &__select {
    width: 320px;
    height: 44px;
    margin: 0 0 20px 0;
    padding-top: 13px;
    padding-bottom: 13px;
    padding-left:   15px;
    border-radius: 4px;
    border: 1px rgba(31, 32, 65, 0.25) solid;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px;
    font-weight: 400;
    color: #1F2041;
    line-height: 18px;
    text-align-last: left;
    vertical-align: center;
  }

  &__item {
    padding: 0;
    text-align: start;
    line-height: 18px;
    //width: 320px;
    //height: 44px;
    //margin: 0 0 20px 0;
    //padding-top: 13px;
    //padding-bottom: 13px;
    //padding-left: 15px;
    //border-radius: 4px;
    //border: 1px rgba(31, 32, 65, 0.25) solid;
    //font-family: 'Montserrat', sans-serif;
    //font-size: 14px;
    //font-weight: 400;
    //color: #1F2041;
  }

  &__select:hover {
    border-color: rgba(31, 32, 65, 0.5);
  }

  &__select:focus {
    outline: rgba(31, 32, 65, 0.5);
  }
}